<template>
  <div class="basic-table">
    <div class="table-container">
      <Table
        :row-class-name="rowClassName"
        :columns="columns1"
        :data="data1"
        @on-sort-change="onSortChange"></Table>
    </div>
    <div class="page-container">
      <Page
        :page-size='pageConfig.pageSize'
        :show-total="pageConfig.showTotal"
        :show-elevator="pageConfig.showElevator"
        :total="pageConfig.pageTotal"
        @on-change="pageChange"
      ></Page>
    </div>
  </div>
</template>

<script>
import TransactionFactory from '../../mock/TransactionFacory'
export default {
  name: 'basic-table',
  props: ['tableColumn', 'tableData', 'pageConfigs'],
  data () {
    return {
      columns1: [
        {
          title: '序号',
          key: 'index',
          ellipsis: true,
          align: 'center'
        },
        {
          title: '交易哈希',
          key: 'hash',
          ellipsis: true,
          align: 'center'
        },
        {
          title: '币种',
          key: 'coin',
          ellipsis: true,
          align: 'center'
        },
        {
          title: '时间',
          key: 'date',
          ellipsis: true,
          align: 'center',
          sortable: true
        },
        {
          title: '发起方',
          key: 'initiator',
          ellipsis: true,
          align: 'center'
        },
        {
          title: '接收方',
          key: 'receiver',
          ellipsis: true,
          align: 'center'
        },
        {
          title: '额度',
          key: 'amount',
          ellipsis: true,
          align: 'center',
          sortable: true
        }
      ],
      data1: [],
      pageConfig: {
        pageSize: 25,
        showTotal: true,
        showElevator: true,
        pageTotal: null
      },
      pageIndex: 1,
      transactions: []
    }
  },
  methods: {
    rowClassName (row, index) {
      return 'table-info-row'
    },
    async getDatas () {
      // 1 set transactions
      // 2 set total number
      const TransactionsCreator = new TransactionFactory()
      let transactions = await TransactionsCreator.getTransactions(60)
      this.transactions = transactions
      this.pageConfig.pageTotal = transactions.length
    },
    pageChange (pageIndex) {
      console.log(pageIndex)
      this.pageIndex = pageIndex
      let offset = (pageIndex - 1) * 25
      this.data1 = this.transactions.slice(offset, offset + 25)
    },
    onSortChange (column) {
      console.log(column)
      if (column.key === 'date') {
        console.log('sort date')
        this.sortMode(column.order)
        // sort data1
        // set data1
      }
    },
    sortMode (order) {
      // 3种模式
      switch (order) {
        case 'desc':
          console.log('desc')
          break
        case 'asc':
          console.log('asc')
          break
        case 'normal':
          console.log('normal')
          break
      }
    }
  },
  async mounted () {
    console.log('[Component] BasicTable')
    await this.getDatas()
    this.data1 = this.transactions.slice(0, 25)
  }
}
</script>

<style>
.ivu-table .table-info-row td{
  cursor: pointer;
}
.page-container {
  width: 100%;
  padding-top: 20px;
}
</style>



