<template>
  <div id="relations-page">
    <app-header></app-header>
    <div class="transaction-form-container">
      <RelationFilterForm></RelationFilterForm>
    </div>
    <div class="basic-table-container">
      <basic-table></basic-table>
    </div>
  </div>
</template>

<script>
import AppHeader from '../components/AppHeader'
import BasicTable from '../components/BasicTable'
import RelationFilterForm from '../components/RelationFilterForm'
export default {
  name: 'relations-page',
  components: {
    AppHeader,
    BasicTable,
    RelationFilterForm
  },
  data () {
    return {}
  },
  mounted () {
    console.log('[View] RelationsPage')
  }
}
</script>

<style>
#relations-page {
  background-color: pink;
  background-color: #f2f4f5;
  padding-bottom: 30px;
}
.transaction-form-container {
}

.basic-table-container {
  width: 80%;
  margin: 30px auto 0;
}
</style>



