<template>
  <div id="transaction-filter-form">
    <Form :model="transactionForm" :rules="transactionRuleValidate" :label-width="80">
      <FormItem label="交易时间">
        <DatePicker type="date" placeholder="选择日期" v-model="transactionForm.date"></DatePicker>
        <span class="amount-input-line"> - </span>
        <DatePicker type="date" placeholder="选择日期" v-model="transactionForm.date"></DatePicker>
      </FormItem>
      <FormItem label="交易类型">
        <RadioGroup v-model="transactionForm.radio">
          <Radio label="全部">全部</Radio>
          <Radio label="流入">流入</Radio>
          <Radio label="流出">流出</Radio>
        </RadioGroup>
      </FormItem>
      <FormItem label="币种">
        <div class="select-container">
          <Select v-model="transactionForm.select">
            <Option value="ETH">ETH</Option>
            <Option value="EOS">EOS</Option>
            <Option value="TRX">TRX</Option>
          </Select>
        </div>
      </FormItem>
      <FormItem label="交易额度">
        <div class="amount-input-container">
          <div class="amount-input">
            <Input v-model="transactionForm.minAmount" number/>
          </div>
          <span class="amount-input-line"> - </span>
          <div class="amount-input">
            <Input v-model="transactionForm.maxAmount" number/>
          </div>
        </div>
      </FormItem>
      <FormItem>
        <Button type="primary">确定</Button>
        <Button type="ghost" style="margin-left: 8px">Cancel</Button>
      </FormItem>
    </Form>
  </div>
</template>

<script>
import { Form, FormItem, DatePicker, Select, Option, RadioGroup, Radio, Input } from 'iview'

export default {
  name: 'transaction-filter-form',
  components: {
    Form,
    FormItem,
    DatePicker,
    Select,
    Option,
    RadioGroup,
    Radio,
    Input
  },
  data () {
    return {
      transactionForm: {
        date: '',
        radio: '全部',
        select: 'ETH',
        minAmount: null,
        maxAmount: null
      },
      transactionRuleValidate: {
      }
    }
  },
  mounted () {
    console.log('[Component]')
  }
}
</script>

<style>

#transaction-filter-form {
  width: 80%;
  padding: 20px;
  margin: 0 auto;
  background-color: #fff;
}
.amount-input-container {
  display: flex;
  flex-flow: row nowrap;
}
.amount-input-line {
  margin: 0 10px;
}
.select-container {
  width: 100px;
}
.ivu-form-item-content {
  text-align: left;
}
.amount-input .ivu-input {
  width: 100px;
}
.ivu-select-single .ivu-select-selection .ivu-select-placeholder, .ivu-select-single .ivu-select-selection .ivu-select-selected-value {
  text-align: center;
}
</style>



