
export default class TransactionFactory {
  constructor () {
    this._name = 'TransactionFactory'
  }
  async createTransaction (index) {
    return {
      id: index,
      index: index,
      hash: this.getHash(32),
      coin: this.getCoin(),
      date: '1522134066395',
      initiator: this.getInitiator(),
      receiver: this.getInitiator(),
      amount: this.getAmount()
    }
  }
  async getTransactions (n) {
    let transactions = []
    for (let i = 0; i < n; i++) {
      let item = await this.createTransaction(i)
      transactions.push(item)
    }
    return transactions
  }
  getHash (n) {
    let hash = ''
    let RandomArr = '0123456789abcdefghijklmnopqrstuvwxyz'.split('')
    for (let i = 0; i < n; i++) {
      hash += RandomArr[Math.floor(Math.random() * RandomArr.length)]
    }
    return hash
  }
  getCoin () {
    let coinArr = ['ETH', 'EOS', 'TRX', 'LTC', 'ADA']
    return coinArr[Math.floor(Math.random() * coinArr.length)]
  }
  getInitiator () {
    let initiatorArr = [
      '023lodb2mdwerchqs1y2smqrtl5lf7dg',
      '123lodb2mdyyuchqs1y2smqrtl5lf7dg',
      'oiylodb2mdyyuchqs1y2smqrtl5lf7dg1',
      '0ielodb2mdyyuchqs1y2smqrtl5lf7dg',
      'erylodb2mdyyuchqs1y2smqrtl5lf7dg'
    ]
    return initiatorArr[Math.floor(Math.random() * initiatorArr.length)]
  }
  getAmount () {
    return Math.floor(Math.random() * 10000)
  }
}



