<template>
  <div id="relation-filter-form">
    <Form :model="relationForm" :rules="relationRuleValidate" :label-width="80">
      <FormItem label="流入金额">
        <div class="amount-input-container">
          <div class="amount-input">
            <Input v-model="relationForm.minInAmount" number/>
          </div>
          <span class="amount-input-line"> - </span>
          <div class="amount-input">
            <Input v-model="relationForm.maxInAmount" number/>
          </div>
        </div>
      </FormItem>
      <FormItem label="流出金额">
        <div class="amount-input-container">
          <div class="amount-input">
            <Input v-model="relationForm.minOutAmount" number/>
          </div>
          <span class="amount-input-line"> - </span>
          <div class="amount-input">
            <Input v-model="relationForm.maxOutAmount" number/>
          </div>
        </div>
      </FormItem>
      <FormItem label="流入交易">
        <div class="amount-input-container">
          <div class="amount-input">
            <Input v-model="relationForm.minInTransaction" number/>
          </div>
          <span class="amount-input-line"> - </span>
          <div class="amount-input">
            <Input v-model="relationForm.maxInTransaction" number/>
          </div>
        </div>
      </FormItem>
      <FormItem label="流出交易">
        <div class="amount-input-container">
          <div class="amount-input">
            <Input v-model="relationForm.maxInTransaction" number/>
          </div>
          <span class="amount-input-line"> - </span>
          <div class="amount-input">
            <Input v-model="relationForm.maxOutTransaction" number/>
          </div>
        </div>
      </FormItem>
      <FormItem label="总交易">
        <div class="amount-input-container">
          <div class="amount-input">
            <Input v-model="relationForm.maxTotalTransaction" number/>
          </div>
          <span class="amount-input-line"> - </span>
          <div class="amount-input">
            <Input v-model="relationForm.maxTotalTransaction" number/>
          </div>
        </div>
      </FormItem>
      <FormItem label="币种">
        <div class="select-container">
          <Select v-model="relationForm.select">
            <Option value="ETH">ETH</Option>
            <Option value="EOS">EOS</Option>
            <Option value="TRX">TRX</Option>
          </Select>
        </div>
      </FormItem>
    </Form>
  </div>
</template>

<script>
import { Form, FormItem, DatePicker, Select, Option, RadioGroup, Radio, Input } from 'iview'

export default {
  name: 'relation-filter-form',
  components: {
    Form,
    FormItem,
    DatePicker,
    Select,
    Option,
    RadioGroup,
    Radio,
    Input
  },
  data () {
    return {
      relationForm: {
        data: '',
        radio: '全部',
        select: 'ETH',
        minInAmount: null,
        maxInAmount: null,
        minOutAmount: null,
        maxOutAmount: null,
        minInTransaction: null,
        maxInTransaction: null,
        minOutTransaction: null,
        maxOutTransaction: null,
        minTotalTransaction: null,
        maxTotalTransaction: null
      },
      relationRuleValidate: {}
    }
  },
  mounted () {
    console.log('[Component]')
  }
}
</script>

<style>
#relation-filter-form {
  width: 80%;
  padding: 20px;
  margin: 0 auto;
  background-color: #fff;
}
.amount-input-container {
  display: flex;
  flex-flow: row nowrap;
}
.amount-input-line {
  margin: 0 10px;
}
.ivu-form-item-content {
  text-align: left;
}
.amount-input .ivu-input {
  width: 100px;
}
.select-container {
  width: 100px;
}
</style>



