<template>
  <div id="detailed-info">
    <div class="tabs-container">
      <Tabs  type="card">
        <TabPane label="交易">
          <basic-table></basic-table>
        </TabPane>
        <TabPane label="关联地址">
          <basic-table></basic-table>
        </TabPane>
        <TabPane label="挖矿">
          <basic-table></basic-table>
        </TabPane>
        <TabPane label="统计分析">
          <basic-table></basic-table>
        </TabPane>
      </Tabs>
    </div>
  </div>
</template>

<script>
import BasicTable from './BasicTable'

export default {
  name: 'detailed-info',
  components: {
    BasicTable
  },
  data () {
    return {
      tableColumns: [
        {
          title: 'Name',
          key: 'name',
          ellipsis: true
        },
        {
          title: 'Age',
          key: 'age',
          ellipsis: true
        },
        {
          title: 'Sex',
          key: 'sex',
          ellipsis: true
        },
        {
          title: 'Address',
          key: 'address',
          ellipsis: true
        }
      ],
      tableData: [
        {
          name: 'lili',
          age: 36,
          sex: 'female',
          address: 'shenzhen'
        },
        {
          name: 'nini',
          age: 23,
          sex: 'female',
          address: 'guangzhou'
        },
        {
          name: 'mimi',
          age: 34,
          sex: 'female',
          address: 'zhongshan'
        },
        {
          name: 'kiki',
          age: 13,
          sex: 'female',
          address: 'foshan'
        }
      ]
    }
  },
  methods: {
    rowClassName (row, index) {
      console.log('[rowClassName] index: ' + index)
      if (index === 1) {
        return 'table-row-style'
      }
      return 'table-row-style'
    }
  },
  mounted () {
    console.log('[Component]')
  }
}
</script>

<style lang="scss" scoped>
@import '../ui/service.scss';
#detailed-info {
  width: 100%;
  background-color: #f2f4f5;
  font-family: $fontFamily;

  .tabs-container {
    width: 80%;
    margin: 0 auto;
    padding: 20px 50px;
    box-sizing: border-box;
    background-color: #fff;
  }
}
</style>



